			<comheader :bgColor="false" :backBtn="false" title="我的"></comheader>	
			<view class="flex align-center px-32">
				<view class="position-relative mr-2">
				    <image v-if="wxHeadPortrait" @click="previewImage" class="rounded-circle flex bgF2F3F5" :src="wxHeadPortrait" mode="aspectFill"  style="width:64px;height:64px;"></image>
				    <image v-else @click="previewImage" class="rounded-circle flex bgF2F3F5" 
						:src="`https://zm.zgzhm.com/mini/img/avatar.png`" mode="aspectFill"  style="width:64px;height:64px;"></image>
				    <image  v-if="getIsLogin" class="position-absolute right-0 bottom-0" src="/static/images/level.png" mode="widthFix" style="width: 20px;height: 20px;"></image>
				</view>
				<template v-if="getIsLogin">
					<view class="flex flex-column flex-1">
						<text class="fs-40 ftffff mb-1">{{getUser.name}}</text>
						<text class="ftffff fs-28">{{getUser.wxPhone  || '-'}}</text>
					</view>
					<view class="flex align-center" @click="navToMyinfo">
						<text class="fs-28 ftffff mr-1">个人资料</text>
						<tui-icon color="#fff" size="20" name="arrowright"></tui-icon>
					</view>
				</template>
				<view v-else class="flex" @tap="tui.href('login',true)">
					<text class="fs-32 f-w-b ftffff">登录/注册</text>
				</view>
			</view>
			<view class="flex align-center justify-around py-3">
				<view class="flex flex-column text-center" @tap="tui.href('/pagesA/intro/intro',true)">
					<text class="fs-28 ftFED3C6 mb-1">等级</text>
					<text class="ftffff fs-32">{{quantity.skillValue || 0}}</text>
				</view>
				<view class="flex flex-column text-center" @tap="tui.href('/pagesA/intro/intro',true)">
					<text class="fs-28 ftFED3C6 mb-1">诚信值</text>
					<text class="ftffff fs-32">{{quantity.integrityValue || 0}}</text>
				</view>
				<view class="flex flex-column text-center" @click="navToFocusList">
					<text class="fs-28 ftFED3C6 mb-1">关注</text>
					<text class="ftffff fs-32">{{quantity.numberOfConcerns || 0}}</text>
				</view>
				<view class="flex flex-column text-center"  @click="navToFansList">
					<text class="fs-28 ftFED3C6 mb-1">粉丝</text>
					<text class="ftffff fs-32">{{quantity.numberOfPeopleConcerned || 0}}</text>
				</view>
			</view>
		</view>
		
		<view class="flex align-center justify-around mx-32 bgffff rounded my-2 py-3">
			<view class="flex flex-column text-center" @click="navToMyDemand">
				<image src="/static/images/my-icon1.png"  class="mx-auto mb-1" mode="widthFix" style="width: 22px;height: 22px;"></image>
				<text class="ft1D2129 fs-24">我的发布</text>
			</view>
			<view class="flex flex-column text-center" @click="navToMyAccept">
				<image src="/static/images/my-icon2.png" class="mx-auto mb-1" mode="widthFix" style="width: 22px;height: 22px;"></image>
				<text class="ft1D2129 fs-24">我的承接</text>
			</view>
			<view class="flex flex-column text-center" @click="navToCompleted">
				<image src="/static/images/my-icon3.png" class="mx-auto mb-1" mode="widthFix" style="width: 22px;height: 22px;"></image>
				<text class="ft1D2129 fs-24">已完成</text>
			</view>
			<view class="flex flex-column text-center position-relative" @click="navToEvaluateList">
				<image src="/static/images/my-icon4.png" class="mx-auto mb-1" mode="widthFix" style="width: 22px;height: 22px;"></image>
				<text class="ft1D2129 fs-24">待评价</text>
				<tui-badge type="danger" absolute :scaleRatio="0.8" translateX="40%" top="-6rpx" v-if="quantity.userEvaluation > 0">{{quantity.userEvaluation}}</tui-badge>
			</view>
		</view>	
		
		<view class="my-nav bgffff rounded mx-32">
			<view class="flex align-center mx-2 py-3 border-bottom" @click="navToAgreement">
				<image src="/static/images/my-nav-icon1.png" mode="widthFix" style="width: 22px;height: 22px;"></image>
				<text class="ft1D2129  fs-28 flex-1 ml-2">服务协议</text>
				<tui-icon color="#86909C" size="20" name="arrowright"></tui-icon>
			</view>
			<view class="flex align-center mx-2 py-3 border-bottom">
				<image src="/static/images/my-nav-icon2.png" mode="widthFix" style="width: 22px;height: 22px;"></image>
				<text class="ft1D2129  fs-28 flex-1 ml-2">新手教程</text>
				<tui-icon color="#86909C" size="20" name="arrowright"></tui-icon>
			</view>
			<view class="mx-2 py-3 border-bottom">
				<button 
				class="flex align-center p-0 sharebtn" 
				type="default" 
				data-name="shareBtn" 
				open-type="share" 
				style="width: 100%;background-color: transparent;line-height: 20px;">
					<image src="/static/images/my-nav-icon3.png" mode="widthFix" style="width: 22px;height: 22px;"></image>
					<text class="ft1D2129  fs-28 flex-1 ml-2 text-left">推荐给好友</text>
					<tui-icon color="#86909C" size="20" name="arrowright"></tui-icon>
				</button>
			</view>
			<view class="flex align-center mx-2 py-3 border-bottom" @click="navToAbout">
				<image src="/static/images/my-nav-icon4.png" mode="widthFix" style="width: 22px;height: 22px;"></image>
				<text class="ft1D2129  fs-28 flex-1 ml-2">关于职苗</text>
				<tui-icon color="#86909C" size="20" name="arrowright"></tui-icon>
			</view>
		</view>	
		
		<login :modal="showLogin" @hide="showLogin = false"></login>
	</view>
</template>

<script>
	import { mapGetters,mapActions } from 'vuex'
	export default {
		data() {
			return {
				showLogin:false,
				wxHeadPortrait:'',//头像
				evalTotal:0,
				quantity:{
					gradeIntegral: 0,
					numberOfConcerns: 0,
					numberOfPeopleConcerned: 0,
					skillQuantity: 0,
				}
			}
		},
		computed:{
			...mapGetters(['getIsLogin','getUser'])
		},
		created() {
			if(this.getIsLogin){
				this.wxHeadPortrait  =  this.getUser.wxHeadPortrait
				this.getUserInformationQuantityFn()
			}
		},
		onShareAppMessage(res) {
		    if (res.from === 'button') {
		      // 来自页面内转发按钮
		      console.log(res.target)
		    }
		    return {
		    	title:'职苗',
		    	imageUrl: '/static/images/logo.png',
		    	path: '/pages/index/index', 
		    }
		},
		methods: {
			...mapActions(['getUserInformationQuantity','uploadFileToDisk','updNameAndPortrait','getCurrentLogonInformation']),
			async getUserInformationQuantityFn(){ // 统计数
				try{
					let res = await this.getUserInformationQuantity()
					this.quantity = {...res.data}
				}catch(e){
					//TODO handle the exception
				}
			},
			async updNameAndPortraitFn(portraitId){ //修改头像
				try{
					let data = {
						name:this.getUser.name,
						portraitId:portraitId
					}
					let res = await this.updNameAndPortrait(data)
					this.tui.toast(res.msg)
					this.getCurrentLogonInformation()
				}catch(e){
					this.tui.toast(e.data.msg)
					//TODO handle the exception
				}
			},
			previewImage(){
				if(!this.getIsLogin){
					this.tui.href('login',true)
					return
				}
				let that = this
				uni.chooseImage({
					count: 1, //默认9
					sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
					sourceType: ['album'], //从相册选择
					success: function (res) {
						console.log(JSON.stringify(res.tempFilePaths));
						that.myUpload(res.tempFilePaths[0])
					}
				});
			},
			myUpload(rsp) {
				console.log(rsp)
				this.wxHeadPortrait = rsp; //更新头像方式一
				this.uploadFileToDisk().then(url => {
					this.tui.uploadFile(url,rsp,{
						operationCode:"userHeaderImage"
					}).then(res => {
						// console.log(res)
						this.updNameAndPortraitFn(res.id)
					})
				})
			},
			navToFocusList(){
				if(!this.getIsLogin){
					this.tui.href('login',true)
					return
				}
				uni.navigateTo({
					url:'/pagesA/focus-list/focus-list'
				})
			},
			navToFansList(){
				if(!this.getIsLogin){
					this.tui.href('login',true)
					return
				}
				uni.navigateTo({
					url:'/pagesA/fans-list/fans-list'
				})
			},
			navToMyDemand(){
				if(!this.getIsLogin){
					this.tui.href('login',true)
					return
				}
				uni.navigateTo({
					url:'/pagesA/mydemand/mydemand'
				})
			},
			navToMyAccept(){
				if(!this.getIsLogin){
					this.tui.href('login',true)
					return
				}
				uni.navigateTo({
					url:'/pagesA/myaccept/myaccept'
				})
			},
			navToCompleted(){
				if(!this.getIsLogin){
					this.tui.href('login',true)
					return
				}
				uni.navigateTo({
					url:'/pagesA/completed/completed'
				})
			},
			navToAgreement(){
				uni.navigateTo({
					url:'/pagesA/agreement/agreement?type=1'
				})
				// uni.navigateTo({
				// 	url:'/pagesA/web-view/hybrid?url=serviceagreement&title=用户服务协议'
				// 	// url:'/pagesA/web-view/hybrid?url=releaserules&title=发布规则'
				// 	// url:'/pagesA/web-view/hybrid?url=privacyagreement&title=隐私政策'
				// })
			},
			navToEvaluateList(){
				uni.navigateTo({
					url:'/pagesA/evaluate/list'
				})
			},
			navToMyinfo(){
				if(!this.getIsLogin){
					this.tui.href('login',true)
					return
				}
				uni.navigateTo({
					url:'/pagesA/myinfo/myinfo'
				})
			},
			navToAbout(){
				uni.navigateTo({
					url:'/pagesA/web-view/web-view?url=https://mp.weixin.qq.com/s/0VEPpckd4_1RZfmQDcsVPQ'
				})
			}
		}
	}
</script>

<style lang="scss">
.my-page .sharebtn:after{
	border: 0;
}
</style>
